	methods: {
		searchEvent : function(name){
			this.fetchContacts(name)
		},
		fetchContacts : function(name){
			this.contactlist = [];
			this.isProcessing = true;
			var url = projectData;
			
			if (name == 'all'){
				this.contactlist = projectData;	
			}else {
				this.contactlist = url.filter((data) => {
					return data.type == name
				})
			}
		}
